#first_page {
    background-color: #2b2b2b;
    display: flex;
    position: relative;
    align-items: flex-start;
    font-size: calc(10px + 2vmin);
    height: 100vh;
    justify-content: left;
    scroll-snap-align: start;
    box-shadow: inset 0 0 500px 500px rgba(0, 0, 0, 0.5);
}

#myNameParent {
    text-align: center;
    position: absolute;
    height: 100%;
    width: 100%;
}
#myName {
    font-family: 'Montserrat',serif;
    color: white;
    z-index: 10;
    position: absolute;
    font-size: 9.4vw;
    left: 50%;
    transform: translateX(-50%);
    top: 40%;
    animation: fadein 5s;

}
@keyframes fadein {
    from {
        opacity:0;
    }
    to {
        opacity:1;
    }
}