#third_page {
    /*background-color: #2d2f37;*/
    background: 0 0 / contain black;
    position: relative;
    z-index: 100;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
    scroll-snap-align: start;
}

#third_page::after {
    background: 0 0 / contain #2d2f37;
    z-index: 100;
    min-height: 100vh;
    display: flex;
    position: absolute;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
    scroll-snap-align: start;
}
